  padding-bottom: 1rem;
  text-align: left;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
}
.table[data-v-1f5f08cc] {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-spacing: 0px;
  background-color: #666666;
  color: #000000;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  -webkit-user-select: none;
}
td[data-v-1f5f08cc], th[data-v-1f5f08cc] {
  margin: 0px;
  border: 2px solid;
  border-color: #eeeeee #999999 #999999 #eeeeee;
  background-color: #cccccc;
  padding: 0px;
  vertical-align: middle;
  text-align: center;
}
td[data-v-1f5f08cc] {
  box-sizing: border-box; /* avoids jumpiness when zoomed */
  width: 1.5rem;
