  min-width: 1.5rem;
  height: 1.5rem;
  min-height: 1.5rem;
  max-height: 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  line-height: 1.1rem;
  cursor: pointer;
}
img[data-v-1f5f08cc] {
  width: 1.5rem;
  height: 1.5rem;
  margin: 2px;
