  padding: 0.3rem;
  vertical-align: middle;
  cursor: pointer;
}
.exposed[data-v-1f5f08cc] {
  background-color: #bbbbbb;
  border-width: 1px;
  border-color: #999999;
  padding: 1px;
  cursor: default;
}
.exploded[data-v-1f5f08cc], .incorrect[data-v-1f5f08cc] {
